    import headTop from '@/components/base/header/head';
    export default {
        components: {
            headTop
        },
        data() {
            return {
                show: false,   // 默认自定义弹窗隐藏
                startTime: null,  //  开始时间
                entTime: null,  //  结束时间
                yesterdayList: [],
                limit: 10,   //  限制每次接收页面
                page: 1,
                maxPage: 1,
                loadText: '下拉加载更多',        //   下拉提示词
                loading: true,                  //     下拉圈圈
                onFetching: false,
            }
        },
        mounted(){
            this.startTime = this.common.commonFun.get_date(1);
            this.entTime = this.common.commonFun.get_date(1);
            this.getInfo();
        },
        methods:{
            // 数据初始化
            getInfo(){
                if (this.onFetching) {
                    // do nothing
                } else {
                    this.onFetching = true;
                    if(this.page > this.maxPage){
                        this.onFetching = false;
                        return;
                    }
                    this.common.commonFun.ajax({
                        dom: this,
                        url: this.common.apiUrl.getMyErr,
                        type: "post",
                        data: {
                            page: this.page,
                            limit: this.limit,
                            start: this.startTime,
                            entTime: this.entTime,
                        },
                        success: (response, allResponse) => {
                            console.log('response',response)
                            this.onFetching = true;
                            this.maxPage = Math.ceil(allResponse.count/this.limit) || 1;
                            if(this.page >= this.maxPage){
                                this.onFetching = false;
                                console.log('滚动结束');
                                this.loadText = "已经到底了哦~";
                                this.loading = false;
                            }
                            this.yesterdayList = this.yesterdayList.concat(response);
                            this.onFetching = false;
                            this.page ++;
                        }
                    });
                }
            },
//            分页符切换
            onItemClick (index){
                this.startTime = this.common.commonFun.get_date(index);
                this.entTime = this.common.commonFun.get_date(index);
                this.page = 1;
                this.loadText = "下拉加载更多";
                this.loading = true;
                this.yesterdayList = [];
                this.$refs.scrollerBottom.reset({top: 0});
                this.getInfo();
            },
            // 点击自定义弹窗时间筛选弹窗
            onItemClicks(){
                this.show = true
            },
//            自定义弹窗=>点击确定
            onConfirm(){
                console.log('我点击了确定',this.startTime,this.entTime)
                this.page = 1;
                this.loadText = "下拉加载更多";
                this.loading = true;
                this.yesterdayList = [];
                this.$refs.scrollerBottom.reset({top: 0});
                this.getInfo();
            },
//            查原由
            origin(index){
                console.log("我要查原由了",index)
                this.$router.push({
                    name: "lookReason",
                    params:{
                        complaint: 'complaint'
                    }
                })
            }
        }
    }



